import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import CommonTable from "../../pages/table/CommonTable";
import CommonTableColumn from "../../pages/table/CommonTableColumn";
import CommonTableRow from "../../pages/table/CommonTableRow";
import { postList } from "../../Data";
import axios from "axios";

const PostList = (props) => {
  const [dataList, setDataList] = useState([]);

  useEffect(() => {
    axios
      .get("http://localhost:8000/api/getInquerys")
      .then((response) => {
        setDataList(response.data.response);
      })
      .catch((Error) => {
        console.log(Error);
      });
  }, []);

  return (
    <>
      <CommonTable headersName={["글번호", "제목", "등록일", "조회수"]}>
        {dataList
          ? dataList.map((item, index) => {
              return (
                <CommonTableRow key={index}>
                  <CommonTableColumn>{item.no}</CommonTableColumn>
                  <CommonTableColumn>
                    <Link to={`/postView/${item.no}`}>{item.title}</Link>
                  </CommonTableColumn>
                  <CommonTableColumn>{item.createDate}</CommonTableColumn>
                  <CommonTableColumn>{item.readCount}</CommonTableColumn>
                </CommonTableRow>
              );
            })
          : ""}
      </CommonTable>
    </>
  );
};

export default PostList;
